// CONTENT
// home page
.b-screen-about{
	&__txt{
		padding-right: 405px;
	}
	&__img{
		width: 355px;
	}
}
// sub pages
.b-two-col{
	&__txt{
		margin-right: 405px;
	}
	&__img{
		width: 355px;
	}
}
// contacts page
.b-contact-modal{
	padding-right: 64px;
	&__form{
		width: auto;
		padding: 37px 0 0 142px;
		float: none;
	}
	&__txt{
		width: auto;
		padding: 61px 0 0;
		float: none;
	}
}
// gallery page
.b-gallery-page{
	&__nav{
		width: 304px;
		padding: 0 50px 0 0;
	}
	&__photo{
		width: calc(100% - 304px);
	}
}
.b-grid {
	margin: 0 -20px;
	&__item{
		padding: 0 20px;
		margin-bottom: 40px;
	}
}
.b-img-post{
	&:hover{
		.b-img-post__img{
			transform: scale(1.7);
		}
	}
}
.b-pagenav{
	padding: 50px 0 0;
	.prev{
		margin: 0 40px 0 0;
	}
	.next{
		margin: 0 0 0 40px;
	}
}
.b-gallery-modal{
	&__img{
		width: 377px;
		padding: 0 50px 0 0;
	}
	&__txt{
		width: calc(100% - 377px);
		padding: 0 50px 0 0;
	}
	&__foot{
		padding: 20px 0 0;
	}
	p{
		padding: 21px 0 0;
	}
}
