// base.scss
body{
	line-height: 18px;
}
.b-center {
	padding-left: 50px;
}
.b-tit{
	font-size: 14px;
	line-height: 18px;
    &--lg{
        font-size: 21px;
        line-height: 25px;
        letter-spacing: 1px;
    }
    &--caps{
        font-size: 30px;
        line-height: 36px;
    }
}
.b-entry{
	p{
		padding: 19px 0 0;
	}
}
.b-tit-left{
	width: 200px;
	height: 10px;
	font-size: 8px;
	left: -84px;
	&--main{
        bottom: 160px;
    }
}
.b-share-nav{
    font-size: 12px;
    &__item{
        padding: 10px 0;
    }
}
// form.scss
.b-input{
	input{
		font-family: 10px;
	}
}
.b-textarea{
	textarea{
		font-size: 10px;
	}
}
.b-form-row{
	font-size: 10px;
	.b-label{
		width: 110px;
		padding: 7px 0 0;
	}
	.b-input{
		width: calc(100% - 110px);
	}
	&--bot{
		margin-top: 7px;
	}
	&--textarea{
		.b-textarea{
			&:before{
				left: 110px;
			}
			textarea{
				text-indent: 110px;
			}
		}
	}
}
.b-btn{
	min-width: 77px;
	height: 30px;
	font-size: 8px;
	line-height: 28px;
}
// CONTENT
.b-img-area{
    &__bg{
        top: 52px;
    }
    &__nav{
    	top: 52px;
    }
}
// home page
.b-home-screen{
	.b-tit{
		padding: 0 0 12px;
		&--lg{
			padding: 0 0 30px;
		}
	}
}
.b-screen-main{
	.b-flex{
		padding-top: 0;
	}
}
.b-screen-about{
	padding-bottom: 30px;
	&__txt{
		padding: 0 0 30px;
	}
	&__entry{
		height: 110px;
	}
	&__btn{
		margin-bottom: -60px;
	}
}
.b-screen-contact{
	font-size: 12px;
	line-height: 24px;
	.b-tit{
		padding: 0 0 37px;
		text-align: left;
		&--lg{
			padding: 0 0 60px;
			text-align: center;
		}
	}
	&__entry{
		text-align: left;
	}
	&__nav{
		padding: 0 0 43px;
	}
}
// sub pages
.b-txt-area{
    padding: 8px 0 30px;
}
.b-two-col{
	padding-bottom: 30px;
	&__img{
		margin-bottom: -30px;
	}
}
// contacts page
.b-address{
	font-size: 10px;
	line-height: 20px;
	span{
		padding-right: 0;
	}
}
.b-message{
	font-size: 10px;
	line-height: 12px;
	display: block;
	&__txt{
		width: auto;
		padding-bottom: 18px;
	}
	&__btn{
		width: auto;
		margin-bottom: -60px;
	}
}
.b-contact-modal{
	&__txt{
		font-size: 10px;
	}
}
// gallery page
.b-filter{
	font-size: 10px;
	line-height: 12px;
}
.b-category-nav{
	min-height: 26px;
	font-size: 10px;
	line-height: 12px;
	&__item{
		&.init{
			height: 26px;
			padding-top: 7px;
			padding-bottom: 7px;
			&:after{
				width: 6px;
	            height: 6px;
	            margin-top: -3px;
	        }
		}
	}
}
.b-grid {
	margin: 0;
	&__item{
		width: 100%;
		padding: 0;
		margin-bottom: 30px;
		float: none;
	}
}
.b-pagenav{
	padding: 30px 0 0;
	.number,
	.is-current{
		display: none;
	}
	.prev{
		margin: 0 20px 0 0;
	}
	.next{
		margin: 0 0 0 20px;
	}
}
.b-gallery-modal{
	&__txt{
		font-size: 10px;
	}
	&__foot{
		font-size: 12px;
		&-price{
			font-size: 14px;
		}
	}
	.b-tit{
		text-align: center;
	}
	p{
		padding: 25px 0 0;
	}
}