@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "base";
@import "vendor";
.b-page{
	min-height: 100vh;
	position: relative;
	overflow-x: hidden;
	z-index: 10;
	@extend .b-bg-gradient;
	&__header{
		width: 100%;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 10;
	}
	&__content{
		padding: 9.083em 0 0;
		position: relative;
		z-index: 5;
	}
	&--home{
		.b-page__header{
			padding-top: 8.333em;
			transition: padding 0.3s ease;
			.b-header__menu{
				top: -6.333em;
			}
			&.fix{
				padding-top: 2.000em;
				.b-header__menu{
					top: 0px;
				}
			}
		}
		.b-page__content{
			padding-top: 0;
		}
	}
}

// HEADER
.b-header{
	padding: 2.000em 0;
	.b-center{
		padding-left: 0;
		@extend .b-flex;
		@extend .b-flex--middle;
	}
	&__logo{
		width: 16.667em;
		position: relative;
		z-index: 10;
	}
	&__menu{
		width: calc(100% - 16.667em);
		text-align: right;
		transition: top 0.3s ease;
		position: relative;
		top: 0;
		z-index: 5;
	}
	&.fix{
		.b-header__menu{
			top: -8.333em;
		}
	}
}
.b-logo{
	position: relative;
	a{
		width: 12.000em;
		height: 5.417em;
		display: block;
		background: $color-black;
		text-indent: -9999em;
		position: relative;
		overflow: hidden;
		&:before,
		&:after{
			display: block;
			content: '';
			position: absolute;
		}
		&:before{
			width: 4.000em;
			height: 1.917em;
			margin-top: -0.917em;
			background: 0 0 url(../images/svg/logo.svg) no-repeat;
			background-size: 100% 100%;
			top: 50%;
			right: 2.583em;
			z-index: 10;
		}
		&:after{
			width: 100%;
			height: 100%;
			background: $color-red;
			top: 0;
			left: 0;
			z-index: 1;
		}
	}
}
.b-menu{
	text-transform: uppercase;
	white-space: nowrap;
	position: relative;
	z-index: 5;
	&__tit{
		padding: 0 0 77px;
		display: none;
		text-transform: none;
		color: $color-black;
	}
	&__item{
		padding: 0 0 0 5.417em;
		display: inline-block;
		vertical-align: top;
		a{
			display: block;
			color: $color-grey;
			text-decoration: none;
			position: relative;
			&:before{
				width: 0;
				height: 0.09em;
				display: block;
				content: '';
				background: $color-red;
				transition: all 0.3s ease;
				transform: translate(-50%, 0);
				position: absolute;
				top: 0;
				left: 50%;
			}
			&:hover{
				&:before{
					width: 2.500em;
				}
			}
		}
		&.is-active{
			a{
				&:before{
					width: 2.500em;
				}
			}
		}
	}
}
.b-menu-btn{
	width: 40px;
	height: 40px;
	margin-right: -10px;
	padding: 10px;
	display: none;
	cursor: pointer;
	position: relative;
	z-index: 6;
	&__line{
		width: 20px;
		height: 1px;
		display: block;
		position: absolute;
		top: 50%;
		right: 10px;
		overflow: hidden;
		&:before,
		&:after{
			height: 100%;
			display: block;
			content: '';
			background: $color-black;
			transition: all 0.3s ease;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
		}
		&:after{
			left: 100%;
		}
		&:nth-child(1){
			margin-top: -5px;
		}
		&:nth-child(2){
			&:before{
				left: 5px;
			}
		}
		&:nth-child(3){
			margin-top: 5px;
			&:before{
				left: 10px;
			}
		}
	}
	&:hover{
		.b-menu-btn__line{
			&:before,
			&:after{
				background: $color-red;
			}
			&:nth-child(2){
				&:before{
					left: 0;
					right: 5px;
				}
				&:after{
					left: 19px;
				}
			}
			&:nth-child(3){
				&:before{
					left: 0;
					right: 10px;
				}
				&:after{
					left: 14px;
				}
			}
		}
	}
	&.is-open{
		.b-menu-btn__line{
			display: none;
			&:after{
				left: 100%;
			}
			&:nth-child(2){
				display: block;
				transform: rotate(45deg);
				&:before{
					left: 0;
					right: 0;
				}
			}
			&:nth-child(3){
				display: block;
				margin: 0;
				transform: rotate(-45deg);
				&:before{
					left: 0;
					right: 0;
				}
			}
		}
	}
}
// CONTENT
.b-loading{
	width: 100%;
	height: 100vh;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 110;
	@extend .b-bg-gradient;
	&__logo{
		position: absolute;
		top: 8.333em;
		left: 0;
	}
	&__number{
		font-size: 2.500em;
		line-height: 1.000em;
		color: $color-black;
		position: absolute;
		bottom: 2.000em;
		right: 1.833em;
	}
	.b-logo{
		a{
			&:after{
				animation: bg-logo 1s linear alternate;
			}
		}
	}
}
@keyframes bg-logo {
    0% {
        width: 0;
    }
    100% {
        width: 100%;
    }
}
.b-txt-area{
    min-height: calc(100vh - 9.083em);
    padding: 2.250em 0;
    position: relative;
    @extend .b-flex;
    @extend .b-flex--middle-col;
}
.b-img-area{
	max-height: calc(100vh - 13.750em);
    padding: 0 0 0 6.667em;
    overflow: hidden;
    position: relative;
    &__bg{
        width: 6.667em;
        display: block;
        background: $color-red;
        position: absolute;
        top: 6.083em;
        bottom: 0;
        left: 0;
        z-index: 2;
    }
    &__nav{
        width: 5.714em;
        position: absolute;
        top: 6.083em;
        bottom: 0;
        left: 0;
        @extend .b-flex;
        @extend .b-flex--middle;
    }
    img{
        width: 100%;
        display: inline-block;
        vertical-align: bottom;
        position: relative;
        z-index: 3;
    }
    &--top{
    	img{
    		vertical-align: top;
    	}
    }
}
// home page
.b-cloud{
	width: 100%;
	height: 100vh;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 9;
	&--one{
		background: 0 0 url(../images/cloud01.png) no-repeat;
		background-size: cover;
		z-index: 15;
	}
	&--two{
		background: 0 0 url(../images/cloud02.png) no-repeat;
		background-size: cover;
		z-index: 15;
	}
	&--three{
		background: center bottom url(../images/cloud03.png) no-repeat;
		
	}
	&--four{
		background: 0 center url(../images/cloud04.png) no-repeat;
	}
	&--five{
		background: right top url(../images/cloud05.png) no-repeat;
	}
}
.b-mouse{
	margin: 0 0 0 -0.584em;
	display: block;
	color: $color-black;
	text-align: center;
	position: absolute;
	bottom: 5.583em;
	left: 50%;
	&:before,
	&:after{
		display: block;
		font-family: $font-family-icomoon !important;
		speak: none;
		font-style: normal;
		font-weight: normal;
		font-variant: normal;
		text-transform: none;
		line-height: 1;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	}
	&:before{
		content: '\e906';
		animation: arrow-mouse 1s infinite;
		transform: translate(-50%, 0);
		bottom: -0.417em;
		left: 50%;
		position: absolute;
	}
	&:after{
		content: '\e905';
		font-size: 2.333em;
		position: relative;
	}
}
@keyframes arrow-mouse {
	0%{
		bottom: -0.333em;
	}
	50%{
		bottom: -0.833em;
	}
  	100%{
  		bottom: -0.333em;
  	}
}

.b-nav-screen{
	transform: translate(0, -50%);
	position: fixed;
	top: 50%;
	right: 2.083em;
	z-index: 100;
	li{
		padding: 0.583em 0;
		a{
			width: 0.833em;
			height: 0.833em;
			display: block;
			border: 0.09em solid $color-body;
			transform: rotate(45deg);
			text-indent: -9999em;
			position: relative;
			overflow: hidden;
			&:hover{
				border-color: $color-red;
				background: $color-red;
			}
			&.active{
				background: $color-body;
				&:hover{
					border-color: $color-body;
					background: $color-body;
				}
			}
		}
	}
}
.b-home-screen{
	.b-tit{
		padding: 0 0 1.000em;
		&--lg{
			padding: 0 0 1.167em;
		}
	}
}
.b-screen-main{
	background: center center url(../images/bg-main.jpg) no-repeat;
	background-size: cover;
	.b-flex{
		height: 100vh;
		padding-top: 9.167em;
	}
}
.b-screen-about{
	.b-center{
		min-height: 100vh;
		padding-top: 9.167em;
	}
	&__txt{
		padding-right: 48.750em;
		padding-bottom: 2.500em;
		position: relative;
		z-index: 2;
	}
	&__entry{
		height: 14.167em;
		margin: 0 0 3.667em;
		position: relative;
		overflow: hidden;
		p{
			&:first-child{
				text-indent: 2.500em;
			}
		}
	}
	&__btn{
		text-align: right;
	}
	&__img{
		width: 42.083em;
		max-height: calc(100vh - 21.583em);
		position: absolute;
		bottom: 0;
		right: 4.667em;
		z-index: 1;
	}
}
.b-screen-contact{
	text-align: center;
	.b-center{
		min-height: 100vh;
		padding-top: 9.167em;
		padding-bottom: 2.500em;
	}
	&__entry{
		padding: 0 0 3.571em;
		font-size: 1.167em;
		span{
			padding: 0 1.429em 0 0;
			white-space: nowrap;
		}
	}
	&__nav{
		padding: 0 0 2.500em;
	}
}

// sub pages
.b-two-col{
	position: relative;
	&__txt{
		margin-right: 48.750em;
		z-index: 2;
	}
	&__img{
		width: 42.083em;
		position: fixed;
		bottom: 0;
		right: 4.667em;
		z-index: 1;
	}
}
// contacts page
.b-address{
	padding: 3.143em 0 0;
	font-size: 1.167em;
	line-height: 2.143em;
	text-transform: uppercase;
	span{
		padding-right: 1em;
		display: inline-block;
		vertical-align: middle;
		white-space: nowrap;
	}
}
.b-message{
	padding: 3.667em 0 0;
	text-transform: uppercase;
	@extend .b-flex;
	@extend .b-flex--middle;
	&__txt{
		width: calc(100% - 11.714em);
		font-size: 1.167em;
	}
	&__btn{
		width: 12.500em;
	}
}
.b-contact-modal{
	position: relative;
	&__form{
		width: calc(100% - 35.417em);
		padding: 3.083em 13.333em 0 11.833em;
		float: left;
		position: relative;
		&:before{
			width: 6.583em;
			height: 100%;
			display: block;
			content: '';
			background: center center url(../images/contacts-modal.jpg) no-repeat;
			background-size: cover;
			position: absolute;
			top: 0;
			left: 0;
			z-index: 1;
		}
	}
	&__txt{
		width: 30em;
		padding: 8.857em 0 0;
		font-size: 1.167em;
		text-transform: uppercase;
		float: right;
		position: relative;
	}
	.b-tit{
		padding: 0 0 1.000em;
		color: $color-red;
	}
	strong{
		color: $color-black;
	}
}
// gallery page
.b-gallery-page{
	padding: 2.417em 0;
	position: relative;
	&__head{
		padding: 0 0 2.667em;
		position: relative;
		@extend .b-flex;
		@extend .b-flex--bottom;
		&-left{
			width: 30%;
		}
		&-right{
			width: 70%;
			padding: 0 0 0.333em;
			text-align: right;
		}
	}
	&__nav{
		width: 33.667em;
		padding: 0 8.250em 0 0;
		float: left;
		position: relative;
		z-index: 2;
		.b-tit{
			padding-bottom: 1.067em;
		}
		.b-category-nav{
			padding-top: 1.583em;
		}
	}
	&__photo{
		width: calc(100% - 33.667em);
		padding: 2.250em 0 0;
		float: left;
		position: relative;
		z-index: 1;
	}
}
.b-filter{
	line-height: 1.167em;
	text-align: right;
	li{
		display: inline-block;
		vertical-align: middle;
	}
	a{
		margin: 0 0 0 3.083em;
		display: none;
		color: $color-body;
		text-decoration: none;
		&:hover{
			color: $color-red;
		}
		&.active{
			display: inline-block;
			color: $color-body;
		}
	}
	&__arrow{
		margin-left: 1.667em;
		padding: 0.250em 0.417em;
		display: block;
		cursor: pointer;
		position: relative;
		&:before{
            width: 0.667em;
            height: 0.667em;
            margin-top: 0.250em;
            display: block;
            content: '';
            border-top: 0.09em solid $color-body;
            border-left: 0.09em solid $color-body;
            transform: rotate(135deg);
            transition: all 0.3s ease;
            position: relative;
        }
        &.open{
        	&:before{
        		transform: rotate(-45deg);
        	}
        }
	}
	&.open{
		a{
			display: inline-block;
			&.active{
				color: $color-red;
			}
		}
	}
}
.b-category-nav{
	line-height: 1.167em;
	text-transform: uppercase;
	position: relative;
	&__list{
		position: relative;
		z-index: 1;
	}
	&__item{
		padding: 0.583em 2.917em 0 0;
		position: relative;
		&:before{
			width: 0;
			height: 0.09em;
			display: block;
			content: '';
			background: $color-red;
			transition: all 0.3s ease;
			position: absolute;
			top: 50%;
			right: 0;
			z-index: 1;
		}
		a{
			color: $color-body;
			text-decoration: none;
			&:hover{
				color: $color-red;
				text-decoration: none;
			}
		}
		&.init{
			display: none;
		}
		&.is-active,
		&:hover{
			&:before{
				width: 2.333em;
			}
		}
	}
}
.b-grid {
	min-height: 80vh;
	margin: 0 -4.167em;
	position: relative;
	&__item{
		width: 50%;
		padding: 0 4.167em 8.333em;
		float: left;
		position: relative;
		overflow: hidden;
	}
}
.b-img-post{
	padding: 0.833em;
	border: 0.09em solid $color-grey2;
	text-transform: uppercase;
	&__over{
		cursor: pointer;
		position: relative;
		overflow: hidden;
	}
	&__btn{
		padding: 0.09em 0;
		border-bottom: 0.09em solid $color-red;
		text-transform: uppercase;
		color: $color-white;
		transition: all 0.5s ease;
		opacity: 1;
        transform: translate(-50%, -50%);
		position: absolute;
		top: -50%;
		left: 50%;
		z-index: 3;
	}
	&__img{
		text-align: center;
		transition: all 0.3s ease;
		position: relative;
		&:before{
			width: 100%;
			height: 100%;
			display: block;
			content: '';
			background: $color-blue;
			transition: all 0.3s ease;
			opacity: 0;
			position: absolute;
			top: 0;
			left: 0;
			z-index: 2;
		}
		img{
			width: 100%;
			display: block;
			position: relative;
			z-index: 1;
		}
	}
	&__foot{
		height: 2.583em;
		margin: 0.667em 0 0;
		line-height: 2.583em;
		transition: all 0.5s ease;
		position: relative;
		top: 0;
		overflow: hidden;
		&-name{
			width: calc(100% - 9.167em);
			float: left;
		}
		&-info{
			width: 9.167em;
			white-space: nowrap;
			float: right;
			text-align: right;
			span{
				padding: 0 0 0 1.250em;
				&:before{
					padding: 0.09em 0.417em 0 0;
					display: inline-block;
					vertical-align: middle;
				}
			}
		}
	}
	&:hover{
		border: 0.09em solid $color-red;
		.b-img-post__btn{
			top: 50%;
		}
		.b-img-post__img{
			transform: scale(1.4);
			&:before{
				opacity: 0.8;
			}
		}
		.b-img-post__foot{
			top: 2.583em;
		}
	}
}
.b-pagenav{
	padding: 0 0 1.250em;
	font-weight: 500;
	line-height: 1.167em;
	text-align: center;
	position: relative;
	a,
	.is-current{
		display: inline-block;
		vertical-align: top;
		color: $color-body;
		text-decoration: none;
		position: relative;
		&:hover{
			color: $color-red;
			text-decoration: none;
		}
	}
	.number{
		margin: 0 0.500em;
	}
	.is-current{
		margin: 0 0.500em 0 0.833em;
		padding-left: 3.333em;
		color: $color-red;
		text-decoration: none;
		&:before{
			width: 2.500em;
			height: 0.09em;
			display: block;
			content: '';
			background: $color-red;
			position: absolute;
			top: 50%;
			left: 0;
		}
	}
	.prev{
		margin: 0 8.333em 0 0;
	}
	.next{
		margin: 0 0 0 8.333em;
	}	
}
.b-gallery-modal{
	text-transform: uppercase;
	position: relative;
	@extend .b-flex;
	@extend .b-flex--middle;
	&__img{
		width: 43.917em;
		padding: 0 12.583em 0 0;
		position: relative;
		z-index: 1;
		img{
			width: 100%;
			display: block;
		}
	}
	&__txt{
		width: calc(100% - 43.917em);
		padding: 0 7.167em 0 0;
		position: relative;
		z-index: 2;
	}
	&__foot{
		padding: 2.500em 0 0;
		@extend .b-flex;
		@extend .b-flex--middle;
		&-price{
			width: 50%;
			font-size: 2.000em;
		}
		&-btn{
			width: 50%;
			text-align: right;
		}
		&-icon{
			padding: 0 1.250em 0 0;
			display: inline-block;
			color: $color-body;
			position: relative;
			&:before{
				padding: 0.09em 0.417em 0 0;
				display: inline-block;
				vertical-align: middle;
				transition: all 0.2s ease;
			}
			&.icon-heart{
				&:hover{
					color: $color-red;
					&:before{
						content: "\e903";
						color: $color-red;
					}
				}
			}
		}
	}
	.b-tit{
		color: $color-red;
	}
	p{
		padding: 3.417em 0 0;
	}
}
@media (max-width: 4700px) {
    body {
        font-size: 38px;
    }
}

@media (max-width: 4100px) {
    body {
        font-size: 36px;
    }
}

@media (max-width: 3500px) {
    body {
        font-size: 28px;
    }
}

@media (max-width: 3000px) {
    body {
        font-size: 24px;
    }
}

@media (max-width: 2500px) {
    body {
        font-size: 18px;
    }
}

@media (max-width: 2000px) {
    body {
        font-size: 16px;
    }
}

@media (max-width: 1800px) {
    body {
        font-size: 15px;
    }
}

@media (max-width: 1600px) {
    body {
        font-size: 13px;
    }
}

