html.fp-enabled,
.fp-enabled body {
    margin: 0;
    padding: 0;
    overflow:hidden;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
}
.fullpage-wrapper {
    width: 100%!important;
    transform: none!important;
}
.fp-section {
    width: 100%!important;
    position: absolute;
    left: 0;
    top: 0;
    visibility: hidden;
    opacity: 0;
    z-index: 0;
    transition: all .7s ease-in-out;
    &.active {
        visibility: visible;
        opacity: 1;
        z-index: 1;
    }
}
.fp-slide {
    float: left;
}
.fp-slide, .fp-slidesContainer {
    height: 100%;
    display: block;
}
.fp-slides {
    z-index:1;
    height: 100%;
    overflow: hidden;
    position: relative;
    transition: all 0.3s ease-out;
}
.fp-section.fp-table, .fp-slide.fp-table {
    table-layout:fixed;
    width: 100%;
}
.fp-tableCell {
    width: 100%;
    height: 100%;
}
.fp-slidesContainer {
    float: left;
    position: relative;
}
.fp-scrollable {
	min-height: 100vh;
    overflow: hidden;
    position: relative;
}
.fp-scroller{
    overflow: hidden;
}
.iScrollIndicator{
    border: 0 !important;
}
.fp-notransition {
    -webkit-transition: none !important;
    transition: none !important;
}
#fp-nav {
    position: fixed;
    z-index: 100;
    margin-top: -2.667em;
    top: 50%;
    opacity: 1;
    &.fp-right{
    	right: 2.083em;
    }
    &.fp-left {
	    left: 2.083em;
	}
	li{
		padding: 0.583em 0;
		position: relative;
		a{
			width: 0.833em;
			height: 0.833em;
			display: block;
			border: 0.083em solid $color-body;
			background-color: transparent;
			transform: rotate(45deg);
			transition: all 0.1s ease-in-out;
			position: relative;
			&:hover{
				border: none;
				background-color: $color-red;
			}
			&.active{
				border: none;
				background: $color-body;
				&:hover{
					border: none;
					background-color: $color-body;
				}
			}
			span{
				display: none;
			}
		}
	}
}
.fp-auto-height.fp-section,
.fp-auto-height .fp-slide,
.fp-auto-height .fp-tableCell{
    height: auto !important;
}

.fp-responsive .fp-auto-height-responsive.fp-section,
.fp-responsive .fp-auto-height-responsive .fp-slide,
.fp-responsive .fp-auto-height-responsive .fp-tableCell {
    height: auto !important;
}

.iScrollVerticalScrollbar{
	opacity: 0;
}