// fancybox.scss
.fancybox-toolbar {
	right: 7px;
    top: 10px;
}
.fancybox-content {
    padding: 90px 20px 30px 20px;
}
.fancybox-close-small {
    display: none;
}
.fancybox-navigation {
    display: none;
}
// base.scss
.b-center {
	padding: 0 20px 0 80px;
}
.b-tit{
	font-size: 21px;
    line-height: 25px;
    &--lg{
        font-size: 30px;
        line-height: 36px;
        letter-spacing: 1px;
    }
}
// form.scss
.b-btn{
	min-width: 100px;
	height: 35px;
	padding-left: 8px;
	padding-right: 8px;
	font-size: 10px;
	line-height: 33px;
}
// main.sccs
.b-page{
	&__content{
		padding: 80px 0 0;
	}
	&--home{
		.b-page__header{
			padding-top: 60px;
			.b-header__menu{
				top: -40px;
			}
			&.fix{
				padding-top: 20px;
				.b-header__menu{
					top: 0px;
				}
			}
		}
		.b-page__content{
			padding-top: 0;
		}
	}
}
// HEADER
.b-header{
	padding: 20px 0;
	.b-center{
		padding-left: 0;
	}
	&.fix{
		.b-header__menu{
			top: 0;
		}
	}
}
.b-logo{
	a{
		width: 80px;
		height: 40px;
		&:before{
			width: 30px;
			height: 14px;
			margin-top: -7px;
			right: 20px;
		}
	}
}
.b-menu{
	height: 100%;
	padding: 82px 20px 0 50px;
	display: none;
	white-space: normal;
	background: #e6eef2; 
	background: -moz-linear-gradient(45deg,  #e6eef2 1%, #d5e2ea 100%);
	background: -webkit-linear-gradient(45deg,  #e6eef2 1%,#d5e2ea 100%);
	background: linear-gradient(45deg,  #e6eef2 1%,#d5e2ea 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e6eef2', endColorstr='#d5e2ea',GradientType=1 );
	font-size: 14px;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	&__tit{
		display: block;
	}
	&__item{
		padding: 12px 0;
		display: block;
		text-align: right;
		a{
			padding: 0 0 0 81px;
			display: inline-block;
			vertical-align: middle;
			&:before{
				transform: translate(0, 0);
				top: 50%;
				left: 0;
			}
			&:hover{
				&:before{
					width: 30px;
				}
			}
		}
	}
	&.open-menu{
		display: block;
	}
}
.b-menu-btn{
	display: inline-block;
	vertical-align: middle;
}
// CONTENT
.b-loading{
	&__logo{
		top: 60px;
	}
	&__number{
		font-size: 18px;
		line-height: 18px;
		bottom: 16px;
		right: 20px;
	}
}
// home page
.b-nav-screen,
#fp-nav{
	display: none;
}
.b-home-screen{
	.b-center{
		padding-top: 80px;
	}
	.b-tit{
		&--lg{
			padding: 0 0 50px;
		}
	}
}
.b-screen-about{
	&__entry{
		height: 120px;
		margin: 0 0 37px;
	}
}
.b-screen-contact{
	.b-center{
		display: flex;
	    flex-direction: column;
	    flex-wrap: nowrap;
	    justify-content: center;
	    align-content: stretch;
	}
}
// contacts page
.b-address{
	padding: 31px 0 0;
	line-height: 18px;
}
.b-message{
	padding: 32px 0 0;
}
// contacts page
.b-contact-modal{
	padding-right: 0;
	&__form{
		padding: 11px 0 0 73px;
		&:before{
			width: 53px;
		}
	}
	&__txt{
		padding: 31px 0 0;
	}
}
// gallery page
.b-gallery-page{
	padding: 8px 0 50px;
	&__head{
		padding: 0;
		display: block;
		&-left{
			width: auto;
			padding: 0 0 25px;
		}
		&-right{
			width: auto;
			padding: 0 0 25px;
			text-align: left;
		}
	}
	&__nav{
		padding: 0 0 40px;
		&--row{
			.b-tit{
				width: auto;
				padding: 0 0 25px;
				display: block;
			}
			.b-category-nav{
				width: auto;
				margin: 0;
				display: block;
			}
		}
	}
	&__photo{
		padding: 0;
	}
}
.b-filter{
	display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-content: stretch;
	li{
		display: block;
		&.first{
			a{
				margin-left: 0;
			}
		}
		&.last{
			display: none;
		}
	}
	a{
		margin: 0 0 0 25px;
		display: inline-block;
		&.active{
			color: $color-red;
		}
	}
}
.b-grid {
	margin: 0 -10px;
	&__item{
		padding: 0 10px;
		margin-bottom: 30px;
	}
}
.b-img-post{
	&:hover{
		.b-img-post__img{
			transform: scale(1.7);
		}
	}
}
.b-pagenav{
	.prev{
		margin: 0 20px 0 0;
	}
	.next{
		margin: 0 0 0 20px;
	}
}
.b-gallery-modal{
	display: block;
	&__img{
		width: auto;
		padding: 0 0 30px;
	}
	&__txt{
		width: auto;
		margin: 0;
		padding: 66px 0 0 0;
	}
	&__foot{
		width: 100%;
		padding: 0px 0 0;
		display: flex;
		text-align: left;
		position: absolute;
		top: 0;
		bottom: auto;
		right: 0;
		&-price{
			width: 50%;
			padding-bottom: 0;
		}
		&-btn{
			width: 50%;
			text-align: right;
		}
		&-icon{
			top: 5px;
		}
	}
}