// fancybox.scss
.fancybox-toolbar {
    top: 31px;
}
.fancybox-content {
    padding: 118px 48px 100px 130px;
}
// base.scss
.b-center {
	padding: 0 50px 0 130px;
}
.b-tit{
    &--lg{
        font-size: 48px;
        line-height: 52px;
        letter-spacing: 3px;
    }
    &--caps{
    	font-size: 60px;
    	line-height: 73px;
    	letter-spacing: 4px;
    }
}
.b-entry{
	padding: 0;
	p{
		padding: 22px 0 0;
	}
}
// main.sccs
.b-page{
	&__content{
		padding: 165px 0 0;
	}
	&--home{
		.b-page__header{
			padding-top: 116px;
			.b-header__menu{
				top: -92px;
			}
			&.fix{
				padding-top: 50px;
				.b-header__menu{
					top: 0px;
				}
			}
		}
		.b-page__content{
			padding-top: 0;
		}
	}
}
// HEADER
.b-header{
	padding: 50px 0;
	.b-center{
		padding-left: 0;
	}
}
.b-logo{
	a{
		width: 130px;
	}
}
.b-menu{
	&__item{
		padding: 0 0 0 53px;
	}
}

// CONTENT
.b-loading{
	&__logo{
		top: 116px;
	}
	&__number{
		bottom: 47px;
		right: 47px;
	}
}
.b-txt-area{
    min-height: 10px;
    padding: 17px 0 30px;
    display: block;
}
.b-img-area{
    padding: 0 0 0 34%;
    &__bg{
        width: 34%;
        top: 123px;
    }
    &__nav{
    	width: 34%;
    	top: 123px;
    }
}
// home page
.b-nav-screen,
#fp-nav.fp-right{
	right: 19px;
}
.b-home-screen{
	.b-center{
		padding-top: 166px;
	}
	.b-tit{
		padding: 0 0 22px;
		&--lg{
			padding: 0 0 87px;
		}
	}
}
.b-screen-about{
	padding-bottom: 70px;
	.b-center{
		display: flex;
	    flex-direction: column;
	    flex-wrap: nowrap;
	    justify-content: flex-end;
	    align-content: stretch;
	}
	&__txt{
		padding: 0 0 30px;
	}
	&__entry{
		height: auto;
		margin: 0 0 64px;
	}
	&__btn{
		margin-bottom: -70px;
		text-align: left;
	}
	&__img{
		width: 100%;
		position: relative;
		bottom: 0;
		right: 0;
	}
}
.b-screen-contact{
	.b-tit{
		&--lg{
			padding: 0 0 151px;
		}
	}
	&__entry{
		padding: 0 0 67px;
	}
}
// sub pages
.b-two-col{
	padding-bottom: 70px;
	&__txt{
		margin-right: 0;
	}
	&__img{
		width: 100%;
		margin-bottom: -70px;
		position: relative;
		top: 0;
		bottom: 0;
		right: 0;
	}
}
// contacts page
.b-address{
	padding: 33px 0 0;
}
.b-message{
	padding: 50px 0 0;
	display: block;
	&__txt{
		width: auto;
		padding-bottom: 39px;
	}
	&__btn{
		width: auto;
		margin-bottom: -70px;
	}
}

// gallery page
.b-gallery-page{
	padding: 18px 0 82px;
	&__head{
		padding: 0 0 52px;
	}
	&__nav{
		width: auto;
		padding: 0 0 33px;
		float: none;
		.b-tit{
			padding-bottom: 0;
		}
		.b-category-nav{
			padding-top: 0;
		}
		&--row{
			white-space: nowrap;
			.b-tit{
				width: 42%;
				margin: 0 -2px;
				display: inline-block;
				vertical-align: middle;
				white-space: normal;
			}
			.b-category-nav{
				width: 58%;
				margin: -6px 0 6px -4px;
				display: inline-block;
				vertical-align: middle;
				white-space: normal;
			}
		}
	}
	&__photo{
		width: auto;
		float: none;
	}
}
.b-category-nav{
	min-height: 33px;
	&__list{
		width: 100%;
		&.is-open{
			.b-category-nav__item{
				display: block;
			}
		}
	}
	&__item{
		padding-left: 10px;
		padding-right: 10px;
		display: none;
		&:before{
			display: none;
		}
		&.init{
			height: 33px;
			padding-top: 9px;
			padding-bottom: 9px;
			border-bottom: 1px solid $color-body;
			transition: all 0.3s ease;
			display: block;
			cursor: pointer;
			&:after{
				width: 8px;
	            height: 8px;
	            margin-top: -4px;
	            display: block;
	            content: '';
	            border-top: 1px solid $color-body;
	            border-left: 1px solid $color-body;
	            transform: rotate(-135deg);
	            transition: all 0.3s ease;
	            position: absolute;
	            top: 50%;
	            right: 8px;
			}
			&.is-open{
				border-bottom: none;
	        	&:after{
	        		transform: rotate(45deg);
	        	}
	        }
		}
		&.is-active{
			a{
				color: $color-red;
			}
		}
	}
}
.b-grid {
	margin: 0 -40px;
	&__item{
		padding: 0 40px;
		margin-bottom: 50px;
	}
}
.b-img-post{
	&:hover{
		.b-img-post__img{
			transform: scale(1.5);
		}
	}
}
.b-gallery-modal{
	display: block;
	&__img{
		width: 300px;
		padding: 0;
	}
	&__txt{
		width: auto;
		margin-right: 50px;
		padding: 66px 0 0 0;
	}
	&__foot{
		padding: 0;
		display: block;
		text-align: right;
		position: absolute;
		bottom: 100%;
		right: 0;
		&-price{
			width: auto;
			padding-bottom: 47px;
		}
		&-btn{
			width: auto;
			text-align: right;
		}
		&-icon{
			top: 5px;
		}
	}
}
