@import url('https://fonts.googleapis.com/css?family=Montserrat:300,400,500,700');
@font-face {
    font-family: 'trajaneo';
    src: url('../fonts/trajan-webfont.woff2') format('woff2'),
         url('../fonts/trajan-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?20tknj');
  src:  url('../fonts/icomoon.eot?20tknj#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?20tknj') format('truetype'),
    url('../fonts/icomoon.woff?20tknj') format('woff'),
    url('../fonts/icomoon.svg?20tknj#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}