*,
*::before,
*::after {
    box-sizing: border-box;
}
html,
body {
    width: 100%;
    min-height: 100%;
    height: auto !important;
    padding: 0;
    margin: 0;
    background: $color-white;
    font-family: $font-family-mont;
    font-size: 48px;
    font-weight: 300;
    line-height: 2.000em;
    letter-spacing: 0.083em;
    color: $color-body;
    position: relative;
}
div,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
form,
select,
input,
textarea,
img,
dl,
dt,
dd,
table,
div,
td,
theder,
button,
blockquote,
article,
aside,
audio,
canvas,
command,
datalist,
details,
figcaption,
figure,
footer,
header,
hgroup,
keygen,
mark,
menu,
meter,
main,
nav,
output,
progress,
rp,
rt,
ruby,
section,
source,
summary,
time,
video,
wbr {
    padding: 0;
    margin: 0;
}
article,
aside,
audio,
canvas,
command,
datalist,
details,
figcaption,
figure,
footer,
header,
hgroup,
keygen,
mark,
menu,
main,
meter,
nav,
output,
progress,
rp,
rt,
ruby,
section,
source,
summary,
time,
video,
wbr,
address {
    display: block;
    font-style: normal;
}
ul,
ol,
li {
    list-style: none;
}
*:active,
*:focus,
a,
input,
textarea,
button {
    outline: none !important;
    -moz-appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    box-shadow: none;
}
input,
textarea,
button{
    font-family: $font-family-mont;
    border-radius: 0;
}
textarea{
	resize: none;
}
::-moz-focus-inner{
    border: none;
}
a {
    color: $color-red;
    transition: all 0.3s ease;
    text-decoration: none;
    &:hover{
        color: $color-grey;
    }
}
strong{
	font-weight: 500;
}
::-webkit-input-placeholder {
    opacity: 1;
    transition: opacity 0.3s ease;
}
::-moz-placeholder {
    opacity: 1;
    transition: opacity 0.3s ease;
}
:-moz-placeholder {
    opacity: 1;
    transition: opacity 0.3s ease;
}
:-ms-input-placeholder {
    opacity: 1;
    transition: opacity 0.3s ease;
}
:focus {
    &::-webkit-input-placeholder {
        opacity: 0;
        -moz-transition: opacity 0.3s ease;
    }
}
:focus {
    &::-moz-placeholder {
        opacity: 0;
        -moz-transition: opacity 0.3s ease;
    }
}
:focus {
    &:-moz-placeholder {
        opacity: 0;
        -moz-transition: opacity 0.3s ease;
    }
}
:focus {
    &:-ms-input-placeholder {
        opacity: 0;
        transition: opacity 0.3s ease;
    }
}
.cf {
    &:before,
    &:after {
        content: '';
        display: block;
        overflow: hidden;
    }
    &:after {
        clear: both;
    }
}
.b-flex {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: stretch;
    &--middle {
        align-items: center;
    }
    &--space-between {
        justify-content: space-between;
    }
    &--bottom {
        align-items: flex-end;
    }
    &--end,
    &--end-col {
        align-items: flex-end;
    }
    &--middle-col,
    &--bottom-col,
    &--end-col,
    &--top-col {
        flex-direction: column;
    }
    &--middle-col {
        justify-content: center;
    }
    &--bottom-col {
        justify-content: flex-end;
    }
    &--top-col {
        justify-content: flex-start;
    }
    &--right {
        justify-content: flex-end;
    }
    &--center {
        justify-content: center;
    }
}
.b-center {
	padding: 0 4.667em 0 9.417em;
    position: relative;
    z-index: 10;
}
.b-bg-gradient{
	background: #e6eef2; 
	background: -moz-linear-gradient(45deg,  #e6eef2 1%, #d5e2ea 100%);
	background: -webkit-linear-gradient(45deg,  #e6eef2 1%,#d5e2ea 100%);
	background: linear-gradient(45deg,  #e6eef2 1%,#d5e2ea 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e6eef2', endColorstr='#d5e2ea',GradientType=1 );
}
.b-tit{
	font-size: 2.500em;
	font-weight: 300;
	line-height: 1.133em;
	text-transform: uppercase;
	color: $color-black;
    &--lg{
        font-size: 5.000em;
        font-weight: 700;
        line-height: 1.217em;
        letter-spacing: 0.067em;
        text-transform: none;
        text-align: center;
    }
    &--caps{
        text-transform: uppercase;
    }
}
.b-entry{
	padding: 2.000em 0 0;
	p{
		padding: 2.083em 0 0;
		&:first-child{
			text-indent: 2.500em;
		}
	}
}
.b-letter{
	font-family: $font-family-traj;
	font-size: 2.000em;
    line-height: 1.000em;
}
.b-tit-left{
	width: 25.000em;
	height: 2.000em;
	margin: 1.250em 0 0;
	text-transform: uppercase;
	text-align: center;
	transform: rotate(90deg);
	position: fixed;
	top: 50%;
	left: -10.083em;
	z-index: 10;
    &--screen{
        position: absolute;
    }
    &--main{
        margin: 0;
        text-align: right;
        color: $color-white;
        top: auto;
        bottom: 17.500em;
    }
}

.b-share-nav{
    font-size: 1.167em;
    text-align: center;
    z-index: 5;
    &__list{
        width: 100%;
        text-align: center;
    }
    &__item{
        padding: 0.929em 0;
    }
    a{
        color: $color-white;
        text-decoration: none;
        &:hover{
            color: $color-black;
        }
    }
    &--row{
        .b-share-nav__item{
            padding: 0 0.929em;
            display: inline-block;
            vertical-align: middle;
        }
        a{
            color: $color-grey;
            &:hover{
                color: $color-red;
            }
        }
    }
}
