[class^="icon-"], [class*=" icon-"] {
	&:before{
		font-family: $font-family-icomoon !important;
		speak: none;
		font-style: normal;
		font-weight: normal;
		font-variant: normal;
		text-transform: none;
		line-height: 1;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	}
}
.icon-mouse:before {
  content: "\e905";
}
.icon-mouse-arrow:before {
  content: "\e906";
}
.icon-catalog:before {
  content: "\e904";
}
.icon-eye:before {
	content: "\e901";
}
.icon-heart:before {
	content: "\e902";
}
.icon-heart-full:before {
  content: "\e903";
}
.icon-file:before {
	content: "\e900";
}
