.b-label{
	display: block;
	font-weight: 500;
	color: $color-black;
	text-transform: uppercase;
}
.b-input{
	position: relative;
	&:before,
	&:after{
		height: 0.09em;
		display: block;
		content: '';
		background: $color-body;
		position: absolute;
		bottom: 0.333em;
		left: 0;
		right: 0;
		z-index: 1;
	}
	input{
		width: 100%;
		height: 2.000em;
		display: block;
		border: none;
		background: none;
		font-size: 1.000em;
		line-height: 2.000em;
		color: $color-body;
		position: relative;
		z-index: 2;
	}
}
.b-textarea{
	position: relative;
	&:before,
	&:after{
		height: 0.09em;
		display: block;
		content: '';
		background: $color-body;
		position: absolute;
		left: 0;
		right: 0;
		z-index: 1;
	}
	&:before{
		top: 2.167em;
	}
	&:after{
		bottom: 1.917em;
	}
	span{
		display: block;
		position: relative;
		z-index: 4;
		&:before,
		&:after{
			height: 0.09em;
			display: block;
			content: '';
			background: $color-body;
			position: absolute;
			left: 0;
			right: 0;
			z-index: 1;
		}
		&:before{
			top: 5.333em;
		}
		&:after{
			bottom: 5.083em;
		}
	}
	textarea{
		width: 100%;
		height: 13.333em;
		border: none;
		background: none;
		font-size: 1.000em;
		line-height: 3.167em;
		color: $color-body;
		position: relative;
		overflow: hidden;
		z-index: 5;
	}
}
.b-form-row{
	margin: 0.750em 0 0;
	position: relative;
	.b-label{
		width: 12.500em;
		padding: 0.250em 0 0;
		float: left;
	}
	.b-input{
		width: calc(100% - 12.500em);
		float: left;
	}
	&--bot{
		margin-top: 3.583em;
	}
	&--textarea{
		.b-label{
			position: absolute;
			top: 0.500em;
			left: 0;
		}
		.b-textarea{
			&:before{
				left: 12.500em;
			}
			textarea{
				text-indent: 12.500em;
			}
		}
	}
}
.b-form-btn{
	padding: 4.167em 0 0;
	&__left{
		width: 50%;
	}
	&__right{
		width: 50%;
		text-align: right;
	}
}
.b-btn{
	min-width: 12.500em;
	height: 3.333em;
	display: inline-block;
	background: none;
	border: 0.09em solid $color-body;
	font-size: 1.000em;
	font-weight: 500;
	line-height: 3.167em;
	text-align: center;
	text-transform: uppercase;
	color: $color-black;
	transition: all 0.3s ease;
	text-decoration: none;
	position: relative;
	cursor: pointer;
	&:hover{
		background: $color-red;
		border-color: $color-red;
		color: $color-white;
	}
}

// file 
.jq-file{
	line-height: 1.167em;
    text-transform: uppercase;
    color: $color-body;
    position: relative;
    overflow: hidden;
    input {
        position: absolute;
        top: 0;
        right: 0;
        height: auto;
        margin: 0;
        padding: 0;
        opacity: 0;
        font-size: 8.333em;
        line-height: 1em;
        cursor: pointer;
    }
    .jq-file__name {
        padding-left: 2.083em;
        transition: all 0.3s ease;
        display: block;
        position: relative;
        &:before{
            margin: -0.500em 0 0 0;
            display: block;
            content: "\e900";
            font-family: $font-family-icomoon;
            font-size: 1.167em;
            line-height: 1.000em;
            speak: none;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            transition: all 0.3s ease;
            position: absolute;
            top: 50%;
            left: 0;
        }
    }
    .jq-file__browse{
        display: none;
    }
    &:hover{
        .jq-file__name {
            color: $color-black;
        }
    }
}